<template>
  <div class="ft">
    <div class="icon-list">
      <a href="https://vanthink-ued.github.io/jquery.core.image.upload/upload.html" aria-label="jQuery" class="hint--bottom"><img src="data:image/png;base64," width="60" alt=""></a>
      <a href="https://vanthink-ued.github.io/angular-core-image-upload/upload.html" aria-label="Comming Soon" class="hint--bottom"><img src="data:image/png;base64," width="60" alt=""></a>
      <a href="https://vanthink-ued.github.io/vue-core-image-upload/index.html" class="hint--bottom">
        <img src="data:image/png;base64," width="60" alt=""></a> <a href="http://vanthink-ued.github.io/react-core-image-upload/upload.html" aria-label="React.js" class="hint--bottom"><img src="data:image/png;base64," width="60" alt=""></a>
    </div>
    <p class="other-info">All Rights Reserved By Vanthink-UED</p>
    <p class="lang-list">
      <a href="javascript:;" title="中文文档" :class="{'active': lan == 'cn'}" @click="changeCn"> 🇨🇳 Chinese </a>
      <a href="javascript:;" title="View Enlish Document" :class="{'active': lan == 'en'}"  @click="changeEn"> 🇺🇸 English </a>
    </p>
</div>
</template>

<script>
import vendor from '../lib/vendor'
  
export default {
  data() {
    return {
      lan: window.lan || 'cn'
    };  
  },

  methods: {
    changeCn() {
      vendor.setLocalData('lan', 'cn');
      location.href="./index.html#/cn/home";
      location.reload();
    },

    changeEn() {
      vendor.setLocalData('lan', 'en');
      location.href="./index.html#/en/home";
      location.reload();
    }
  }

};

</script>



